/*
 ** Base styles
 *
 *- NOTE: html is set to 62.5% so that all the REM measurements throughout Skeleton
 *- are based on 10px sizing. So basically 1.5rem = 15px :)
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

*,
*::before,
*::after {
 box-sizing: border-box;
 box-sizing: inherit;
 margin: 0;
 padding: 0; }

html {
 -webkit-text-size-adjust: 100%;
 -ms-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 font-variant-ligatures: common-ligatures;
 -webkit-tap-highlight-color: transparent;
 font-kerning: normal;
 font-family: sans-serif, serif;
 font-size: 62.5%;
 box-sizing: border-box; }

html.no-js {
 body:before {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 content: "No javascript? No Page!"; } }

body {
 -webkit-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 font-kerning: normal;
 font-variant-ligatures: common-ligatures;
 text-rendering: optimizeLegibility;
 overflow-x: hidden;

 background-color: #f5f5f5;
 border-top: 5px solid #333333;
 /* currently ems cause chrome bug misinterpreting rems on body element */
 @include font($systemFF,1.6rem,#333333,400,1.6);
 margin: 0; }


/* margins
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

button, .button {
 margin-bottom: 1rem; }

input, textarea, select, fieldset {
 margin-bottom: 1.5rem; }

pre, blockquote, dl, figure, table, p, ul, ol, form {
 margin-bottom: 2.5rem; }

/* hr
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

hr {
 border: 0;
 height: 25px;
 margin: 4rem 0 6rem;
 //background: url(/assets/images/eagle.png) center center no-repeat
 background-position: center center;
 background-repeat: no-repeat; }
