blockquote {
 position: relative;
 background-color: #f9f9f9;
 border-left: .3rem solid #333333;
 margin: 1.5rem 1rem;
 padding: 1rem 1rem; }
blockquote:before:not([class]) {
 color: #333333;
 content: "\201C";
 font-size: 4rem;
 line-height: 0.1rem;
 margin-right: 0.15rem;
 vertical-align: -.7rem; }
blockquote p {
 display: inline; }
p cite {
 margin-left: 4.5rem;
 font-variant: small-caps;
 font-style: normal;
 @include font(false,false,#568203,600,1.4);
 } //

.highlight {
 padding: 5px 10px;
 border-radius: 6px;
 background-color: rgba(240, 222, 168, 0.3);
 line-height: 1.4;
 code {
 font-size: .7rem; } }

.typewriter {
 font-family: "American Typewriter"; }

.copy, .catalogoLinkFoto {
 display: inline-block;
 @include font(false,1.3rem,false,400,1.4);
 background-color: rgba(143, 190, 54, 0.16);
 border-radius: 5px;
 padding: 5px 10px;
 a {
 @include font(false,false,false,600,1.6); } }

.postSubtitle {
 font-variant: small-caps;
 @include font(false,false,false,600,1.4); }

.daa {
 @include font(false,1.3,#568203,500,1.6); }
