/*
 ** Media
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

img:not([src]):not([srcset]) {
 visibility: hidden; }

@-moz-document url-prefix() {
 img:-moz-loading {
 visibility: hidden; } }

img,
video,
object {
 max-width: 100%;
 height: auto;
 border: none;
 outline: none; }

figure {
 max-width: 100%;
 margin: auto; }

figcaption {
 margin: .5rem auto;
 text-align: center;
 @include font(false, false, false, false, 3);
 border: 1px solid #000000;
 background-color: #fafafa; }

/*
 ** Progressive Image
 * by https://github.com/craigbuckler/progressive-image.js
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.progressive {
 position: relative;
 display: block;
 overflow: hidden;
 outline: none;

 img {
 display: block;
 width: 100%;
 max-width: none;
 height: auto;
 border: 0;

 &.preview {
 filter: blur(2vw);
 transform: scale(1.05); }

 &.reveal {
 position: absolute;
 left: 0;
 top: 0;
 will-change: transform,opacity;
 animation: progressiveReveal 1s ease-out; } } }

@keyframes progressiveReveal {
 0% {
 transform: scale(1.05);
 opacity: 0; }

 to {
 transform: scale(1);
 opacity: 1; } }
