.indexWrapper {
 border-top: 1px solid #333333;
 padding-top: 1rem;
 margin-bottom: 1rem;

 > div {
 width: 100%;
 display: table;
 &:before, &:after {
 content: "";
 display: table-row; } }
 article {
 display: table-row;
 margin-bottom: 10px;
 a {
 display: table-cell;
 line-height: 1.8; }

 .post-date {
 text-align: right;
 display: table-cell;
 time {
 } } } } //

.tagCat {
 display: inline-block;
 background-color: rgba(143, 190, 54, 0.16);
 padding: 0 .5rem;
 margin-right: .5rem;
 border-radius: 4px;
 font-size: 85%;
 transition: all 0.1s ease-in-out;
 &:hover {
 background-color: #333333;
 color: #FFFFFF; } }

.navigationPosts {
 text-align: center;
 a {
 padding: 0.5rem 1rem;
 font-weight: 700;
 display: inline-block;
 border: 1px solid #568203;
 border-radius: 6px;
 &:hover {
 border: none;
 background-color: #333333; } }
 a:first-child {
 margin-right: 1rem; }
 a:last-child {
 margin-left: 1rem; } }

.navCategories {
 text-align: center;
 margin: 2rem auto; }

div.indexWrapper {
