/*
 ** Utilities
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.classTitle {
 @extend %text;
 @extend %titoli; }

.tal {
 text-align: left; }
.tac {
 text-align: center;
 margin-left: auto;
 margin-right: auto; }
.tar {
 text-align: right; }
.taj {
 text-align: justify; }

.clearfix {
 @include clearfix; }

.flow {
 --flow-space: 1em; }

.flow > * + * {
 margin-top: 1em;
 margin-top: var(--flow-space); }

article > * + * {
 margin-top: 1em; }
