/*
 ** Links
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

a {
 background-color: transparent;
 -webkit-text-decoration-skip: objects;
 text-decoration-skip-ink: auto;
 text-decoration: none;
 @include font(false,false,#568203,600,false);
 &:focus,
 &:active,
 &:visited {
 @include font(false,false,#568203,600,false);
 outline-width: 0; }
 &:hover {
 @include font(false,false,#FFFFFF,false,false); } }

a.notClass {
 @include font(false,false,false,400,false);
 &:hover {
 @include font(false,false,#568203,600,false); } }
