/*
 ** Tables
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

table {
 border-collapse: collapse;
 border-spacing: 0;
 border: 1px solid #888888;
 width: 100%; }

/* Modifiche */
thead {
 background-color: #C6C6C6; }

td, th {
 padding: 3px 5px; }

dl {
 display: flex;
 flex-wrap: wrap; }

dt {
 flex-basis: 40%;
 font-weight: 600;
 text-indent: 8px;
 background-color: #d3d3d3; }

dd {
 -ms-flex-preferred-size: 60%;
 flex-basis: 60%;
 text-indent: 8px;
 background-color: #FFFFFF; }

.dlhead {
 border-radius: 7px;
 border-top: solid 1px #000000;
 border-bottom: solid 1px #000000;
 font-weight: 600;
 background-color: #C6C6C6;
 margin-bottom: 16px; }

.dllong {
 flex-basis: 100%;
 font-weight: 500;
 background-color: #FFFFFF;
 margin-top: 32px; }
