/*
 ** Buttons
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.button, button {
 display: inline-block;
 text-align: center;
 letter-spacing: .1rem;
 text-transform: uppercase;
 text-decoration: none;
 white-space: nowrap;
 background-color: transparent;
 border-radius: 4px;
 border: 1px solid #888888;
 cursor: pointer;
 box-sizing: border-box;

 padding: 1rem 2rem;
 background-color: rgba(230, 110, 46, 1); }

.button:focus, button:focus {
 color: #222222;
 border-color: #888888;
 outline: 0; }

.button:hover, button:hover {
 color: #222222;
 border-color: #888888;
 outline: 0; }

input {
 &[type="submit"], &[type="reset"], &[type="button"] {
 display: inline-block;
 text-align: center;
 letter-spacing: .1rem;
 text-transform: uppercase;
 text-decoration: none;
 white-space: nowrap;
 background-color: transparent;
 border-radius: 4px;
 border: 1px solid #888888;
 cursor: pointer;
 box-sizing: border-box; } }

input {
 &[type="submit"]:hover, &[type="reset"]:hover, &[type="button"]:hover {
 color: #222222;
 border-color: #888888;
 outline: 0; } }

input {
 &[type="submit"]:focus, &[type="reset"]:focus, &[type="button"]:focus {
 color: #222222;
 border-color: #888888;
 outline: 0; } }
