/* Headere and Navigation
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

header.siteHeader {
 .wrapperHeader {
 display: flex;
 //margin: 0 0 1rem 0
 div {
 flex-basis: 9rem;
 flex-shrink: 0;
 flex-grow: 0;
 @include size(9rem);
 background-size: 9rem;
 //background-position: center center
 background-repeat: no-repeat;
 border-radius: 50%;
 margin: 1rem 2rem 0 0; }
 nav#navigationMain {
 height: 10rem;
 margin: 0 0 4rem 0;
 display: flex;
 a.navlink {
 display: inline-block;
 margin: -0.5rem 1rem 0 0;
 padding: 6.6rem 2rem 0.5rem;
 border-radius: 0 0 0.5rem 0.5rem;
 border-bottom: 1px solid #333333;
 text-transform: uppercase;
 transition: all ease-in-out 0.3s;
 &.current {
 background-color: #333333; }
 &:hover, &.current {
 color: #f5f5f5;
 border-bottom: none; }
 &:hover {
 background-color: #333333;
 -webkit-transform: translateY(5px);
 transform: translateY(5px); }
 //&.current
 } //background-color: #f5f5f5
 a.trigger {
 display: none; } } } }

@include mediaQ(small) {
 header.siteHeader {
 .wrapperHeader {
 margin: 0 0 0 2rem;
 div {
 flex-basis: 12rem;
 @include size(11rem);
 background-size: 12rem;
 margin: 1rem 2rem 0 0; }
 nav#navigationMain {
 height: 12rem;
 //margin: 0 0 4rem 2.5rem
 a.navlink {
 margin: -0.5rem 2.5rem 0 0; } } } } }
